// =========================================================
// GENEREL PROJECT SETTINGS
// =========================================================

:root  {
    background-color: $color-white;
}

figure  {
    line-height: 0;
}

img {
    max-width: 100%;
    vertical-align: middle;
    line-height: 0;
}

html {
    height: auto;
}

body {
    height: 100%;
    margin: 0px;
}

#app {
    font-family: 'Inconsolata','Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: rgb(65, 65, 65);
    position: relative;
    height: 100%;
}

// =========================================================
// PASS MEDIAQUERIES TO JS
// =========================================================

body {
    &::after {
        @include size(sm-up) {
            content: '2';
        }
        @include size(md-up) {
            content: '3';
        }
        @include size(lg-up) {
            content: '4';
        }
        @include size(xl-only) {
            content: '5';
        }
        position: absolute;
        top: -20000px;
        left: -20000px;
        width: 0;
        height: 0;
        content: '1';
    }
}



