// Based on html5doctor.com Reset Stylesheet v1.6.1
// Adjusted by PwC
// Last Updated: 2010-09-17
// Author: Richard Clark - http://richclarkdesign.com 
// Twitter: @rich_clark
html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    outline: 0;
    border: 0;
    background: transparent;
    padding: 0;
    vertical-align: baseline;
    font-size: 100%;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}

nav {
    ul {
        list-style: none;
    }
}

blockquote,
q {
    quotes: none;
}

blockquote::before,
blockquote::after,
q::before,
q::after {
    content: '';
}

a   {
    margin: 0;
    background: transparent;
    cursor: pointer;
    padding: 0;
    vertical-align: baseline;
    text-decoration: none;
    color: inherit;
    font-size: inherit;
}

// change colours to suit your needs
ins {
    background-color: none;
    text-decoration: none;
    color: currentColor;
}

// change colours to suit your needs
mark {
    background-color: none;
    color: inherit;
    font-weight: bold;
}

del {
    text-decoration: line-through;
}

abbr[title],
dfn[title] {
    border: none;
    cursor: help;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

hr {
    display: block;
    margin: 0;
    border: 0;
    border-top: 1px solid currentColor;
    padding: 0;
    height: 1px;
}

input,
select {
    vertical-align: middle;
}



